export default {
    data() {
        return {
            email: '',
            referrer: this.$cookies.get('manyletter.referrer')
            // referrer: decodeURIComponent(new Buffer(this.$cookies.get('manyletter.referrer'), 'base64').toString('ascii'))
        }
    },
    methods: {
        async submitForm() {
            await this.$axios.$post('/api/maylday/signup', {
                "email": this.email,
                "referrer": this.referrer || ''
            })
            .then((response) => {
                console.log(response);
                this.$router.push('/sign-up/code?email=' + encodeURIComponent(this.email))
            }, (error) => {
                console.log(error);
                //this.res = error.response.data.message
            });
        }
    },
}
